* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

#mf-logo {
  width: 20vw;
  min-width: 200px;
  transition: all 1s;
  display: inline-block;
  position: absolute;
  top: -2em;
  left: 10px;
}

#mf-logo:hover {
  transform: scale(1.2)translateX(10px);
}

nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

nav ul li {
  display: none;
}

nav ul li a {
  color: #ec1a62;
  text-shadow: 0 0 3px #a9a9a9;
}

nav #burger-label, nav #burger-checkbox {
  display: inline;
  position: absolute;
  top: 1.5em;
  right: .5em;
}

nav #burger-checkbox {
  display: none;
}

nav #burger-checkbox:checked + ul {
  width: 100%;
  text-align: center;
  background-color: #38444c;
  flex-direction: column;
  gap: 2rem;
  display: flex;
  position: absolute;
  top: 8em;
  left: 0;
}

nav #burger-checkbox:checked + ul li {
  display: initial;
}

nav a {
  height: 60px;
  color: #fff;
  padding: 20px;
  font-family: Montserrat, sans-serif;
  line-height: 60px;
  position: relative;
}

@media (min-width: 488px) {
  nav {
    justify-content: space-between;
    display: flex;
  }

  nav #burger-label, nav #burger-checkbox {
    display: none;
  }

  nav #mf-logo {
    width: 10vw;
    min-width: 200px;
    transition: all 1s;
    display: inline-block;
    position: absolute;
    top: 3em;
    left: 2em;
  }

  nav #mf-logo:hover {
    transform: scale(1.2)translateX(10px);
  }

  nav ul {
    width: 100%;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 1.5em;
    display: flex;
  }

  nav ul li {
    display: inline-flex;
  }

  nav ul li a {
    font-size: 1.1em;
  }
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  color: #dee2e4;
  background-color: #38444c;
  border: 6px solid #ec1a62;
  font-size: calc(13px + .5vw);
  overflow: auto;
}

.pink-letter {
  display: inline-block;
}

.pink-letter:first-letter {
  color: #ec1a62;
}

.hello-div-parent {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 2em;
  margin-top: 100px;
  display: flex;
}

.hello-div-parent #my-pic {
  object-fit: cover;
  width: 7em;
  height: 7em;
  border: 5px dotted #ec1a62;
  border-radius: 50%;
  margin-top: 5em;
  display: inline-block;
}

.hello-div-parent .hello-div-child h1, .hello-div-parent .hello-div-child h2 {
  text-align: center;
  color: #fff;
  padding-left: 1em;
  padding-right: 1em;
}

.hello-div-parent .hello-div-child h1 {
  margin-bottom: 25px;
  font-family: League Spartan, sans-serif;
}

.hello-div-parent .hello-div-child h2 {
  letter-spacing: .2em;
  font-family: Merriweather Sans, sans-serif;
}

.link-sec {
  font-family: Hind Madurai, sans-serif;
}

.link-sec .container {
  width: 40em;
  max-width: 60vw;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
  line-height: 1.5;
}

.link-sec h3 {
  letter-spacing: 1px;
  padding: 25px 0;
}

.link-sec #check {
  display: none;
}

.link-sec #check:checked ~ .content {
  display: block;
}

.link-sec #check:checked ~ label, .link-sec #check2 {
  display: none;
}

.link-sec #check2:checked ~ .content {
  display: block;
}

.link-sec #check2:checked ~ label, .link-sec #check3 {
  display: none;
}

.link-sec #check3:checked ~ .content {
  display: block;
}

.link-sec #check3:checked ~ label {
  display: none;
}

.link-sec .content {
  transition: all .5s linear;
  display: none;
}

.link-sec label {
  color: #ec1a62;
  cursor: pointer;
  border: 2px solid #ec1a62;
  border-radius: 20px;
  margin-top: 1em;
  padding: .5em;
  font-size: .8rem;
  font-weight: 600;
  display: inline-block;
}

.link-sec .third-label {
  display: none;
}

@media (min-width: 488px) and (max-width: 1467px) {
  .hello-div-parent {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .hello-div-parent #my-pic {
    width: 13em;
    height: 13em;
  }
}

@media (min-width: 1468px) {
  .hello-div-parent {
    height: 100vh;
    flex-direction: row-reverse;
    justify-content: space-evenly;
    align-items: center;
    gap: 2em;
    margin: 0;
    display: flex;
  }

  .hello-div-parent #my-pic {
    width: 13em;
    height: 13em;
    margin-top: 0;
    margin-bottom: 280px;
    margin-right: 200px;
  }

  .hello-div-parent .hello-div-child {
    margin-left: 150px;
  }

  .hello-div-parent .hello-div-child h1 {
    text-align: center;
    margin-top: 100px;
    font-family: Montserrat, sans-serif;
    font-size: 3rem;
  }

  .link-sec .container {
    text-align: start;
  }

  .link-sec .container:nth-child(2n+1) {
    float: right;
    clear: right;
    margin-right: 2em;
  }

  .link-sec .container:nth-child(2n) {
    float: left;
    clear: left;
    margin-left: 2em;
  }
}

#hire {
  clear: both;
  text-align: center;
  width: 100vw;
  max-width: 60vw;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
  font-family: Hind Madurai, sans-serif;
  line-height: 1.5;
  position: relative;
}

#hire p {
  margin-bottom: 2em;
  margin-left: .5em;
}

#hire img {
  width: 50px;
  padding: 3px;
}

#hire div[data-tool-tip]:before {
  content: attr(data-tool-tip);
  background-color: var(--pink-color);
  color: #ec1a62;
  padding: 1em 2em;
  font-family: League Spartan, sans-serif;
  font-size: .8em;
  font-weight: 700;
  transition: transform .55s ease-out, bottom .55s ease-out;
  display: block;
  position: absolute;
  top: 85px;
  left: 500px;
  transform: scale(0);
}

#hire div[data-tool-tip]:hover:before {
  bottom: 100%;
  transform: scale(1);
}

#hire .social-links img {
  margin: 0 auto;
}

